import Vue from "vue";
import Router from "vue-router";
import Login from "@/pages/login";
import Home from "@/pages/home";
Vue.use(Router);
export default new Router({
	routes: [
		{
			path: '/',
			redirect: {path: '/message'}
		},
		{
			path: '/login',
			name: 'login',
			component: Login,
			meta: {
				pageTitle: '登陆',
			}
		},
		{
			path: '/home',
			name: 'home',
			component: Home,
			children: [
				{
					path: '/system',
					name: 'system',
					component: (resolve) => require(['@/pages/system/system.vue'], resolve),
					meta: {
						parentName: 'system',
						activeName: 'system',
						pageTitle: '系统管理',
						privilegeId: ['6000'],
					}
				},
				{
					path: '/edituser',
					name: 'edituser',
					component: (resolve) => require(['@/pages/system/editpower.vue'], resolve),
					meta: {
						parentName: 'system',
						activeName: 'system',
						pageTitle: '权限编辑',
						privilegeId: ['6000']
					}
				},
				{
					path: '/editrole',
					name: 'editrole',
					component: (resolve) => require(['@/pages/system/editrole.vue'], resolve),
					meta: {
						parentName: 'system',
						activeName: 'system',
						pageTitle: '配置角色',
						privilegeId: ['6000']
					}
				},
				{
					path: '/adduser',
					name: 'adduser',
					component: (resolve) => require(['@/pages/system/adduser.vue'], resolve),
					meta: {
						parentName: 'system',
						activeName: 'system',
						pageTitle: '新建账号',
						privilegeId: ['6000']
					}
				},
				{
					path: '/changepsw',
					name: 'changepsw',
					component: (resolve) => require(['@/pages/system/changepsw.vue'], resolve),
					meta: {
						parentName: 'system',
						activeName: 'system',
						pageTitle: '修改密码',
						privilegeId: ['5000']
					}
				},
				{
					path: '/director',
					name: 'director',
					component: (resolve) => require(['@/pages/approval/director/director.vue'], resolve),
					meta: {
						privilegeId: ['1080'],
						parentName: 'approve',
						activeName: 'director',
						pageTitle: '金融专员',
					}
				},
				{
					path: '/orderdetail',
					name: 'orderdetail',
					component: (resolve) => require(['@/pages/approval/director/orderdetail.vue'], resolve),
					meta: {
						privilegeId: ['1080'],
						parentName: 'approve',
						activeName: 'director',
						pageTitle: '贷款申请信息表（金融专员）',
					}
				},
				{
					path: '/creditstart',
					name: 'creditstart',
					component: (resolve) => require(['@/pages/approval/credit/creditstart.vue'], resolve),
					meta: {
						privilegeId: ['1001'],
						parentName: 'approve',
						activeName: 'creditstart',
						pageTitle: '授信初审',
					}
				},
				{
					path: '/creditfinish',
					name: 'creditfinish',
					component: (resolve) => require(['@/pages/approval/credit/creditfinish.vue'], resolve),
					meta: {
						privilegeId: ['1002'],
						parentName: 'approve',
						activeName: 'creditfinish',
						pageTitle: '授信复审',
					}
				},
				{
					path: '/orderinfo',
					name: 'orderinfo',
					component: (resolve) => require(['@/pages/approval/credit/orderinfo.vue'], resolve),
					meta: {
						privilegeId: ['1001','1002'],
						parentName: 'approve',
						activeName: 'approve',
						pageTitle: '贷款申请信息表（授信审核）',
					}
				},
				{
					path: '/loanstart',
					name: 'loanstart',
					component: (resolve) => require(['@/pages/approval/loanapproval/loanstart.vue'], resolve),
					meta: {
						privilegeId: ['1003'],
						parentName: 'approve',
						activeName: 'loanstart',
						pageTitle: '放款初审',
					}
				},
				{
					path: '/loanongoing',
					name: 'loanongoing',
					component: (resolve) => require(['@/pages/approval/loanapproval/loanongoing.vue'], resolve),
					meta: {
						privilegeId: ['1004'],
						parentName: 'approve',
						activeName: 'loanongoing',
						pageTitle: '放款复审',
					}
				},
				{
					path: '/loanfinish',
					name: 'loanfinish',
					component: (resolve) => require(['@/pages/approval/loanapproval/loanfinish.vue'], resolve),
					meta: {
						privilegeId: ['1005'],
						parentName: 'approve',
						activeName: 'loanfinish',
						pageTitle: '放款终审',
					}
				},
				{
					path: '/loaninfo',
					name: 'loaninfo',
					component: (resolve) => require(['@/pages/approval/loanapproval/loaninfo.vue'], resolve),
					meta: {
						privilegeId: ['1003','1004','1005'],
						parentName: 'approve',
						activeName: 'approve',
						pageTitle: '贷款申请信息表（放款审批）',
					}
				},
				{
					path: '/loanconfirm',
					name: 'loanconfirm',
					component: (resolve) => require(['@/pages/approval/loancomfirm/loancomfirm.vue'], resolve),
					meta: {
						privilegeId: ['1006'],
						parentName: 'approve',
						activeName: 'loanconfirm',
						pageTitle: '账务确认',
					}
				},
				{
					path: '/orderloan',
					name: 'orderloan',
					component: (resolve) => require(['@/pages/approval/loancomfirm/orderloan.vue'], resolve),
					meta: {
						privilegeId: ['1006'],
						parentName: 'approve',
						activeName: 'loanconfirm',
						pageTitle: '账务确认信息表',
					}
				},
				{
					path: '/loanconfirmdetail',
					name: 'loanconfirmdetail',
					component: (resolve) => require(['@/pages/approval/loancomfirm/loancomfirmdetail.vue'], resolve),
					meta: {
						privilegeId: ['1006'],
						parentName: 'approve',
						activeName: 'loanconfirm',
						pageTitle: '账务确认详情',
					}
				},
				{
					path: '/inloan',
					name: 'inloan',
					component: (resolve) => require(['@/pages/afterloan/inloan.vue'], resolve),
					meta: {
						privilegeId: ['2001'],
						parentName: 'afterloan',
						activeName: 'inloan',
						pageTitle: '在贷客户',
					}
				},
				{
					path: '/loandetail',
					name: 'loandetail',
					component: (resolve) => require(['@/pages/afterloan/loandetail.vue'], resolve),
					meta: {
						privilegeId: ['2001'],
						parentName: 'afterloan',
						activeName: 'afterloan',
						pageTitle: '贷后详情',
					}
				},
				{
					path: '/outloan',
					name: 'outloan',
					component: (resolve) => require(['@/pages/afterloan/outloan.vue'], resolve),
					meta: {
						privilegeId: ['2002'],
						parentName: 'afterloan',
						activeName: 'outloan',
						pageTitle: '清贷客户',
					}
				},
				{
					path: '/message',
					name: 'message',
					component: (resolve) => require(['@/pages/message/message.vue'], resolve),
					meta: {
						privilegeId: ['5000'],
						parentName: 'message',
						activeName: 'message',
						pageTitle: '我的消息',
					}
				},
				{
					path: '/pawnin',
					name: 'pawnin',
					component: (resolve) => require(['@/pages/pawnmanage/in/pawnin.vue'], resolve),
					meta: {
						privilegeId: ['3001'],
						parentName: 'pawnmanage',
						activeName: 'pawnin',
						pageTitle: '入库',
					}
				},
				{
					path: '/applyin',
					name: 'applyin',
					component: (resolve) => require(['@/pages/pawnmanage/in/applyin.vue'], resolve),
					meta: {
						privilegeId: ['3001'],
						parentName: 'pawnmanage',
						activeName: 'pawnin',
						pageTitle: '入库申请',
					}
				},
				{
					path: '/pawninfo',
					name: 'pawninfo',
					component: (resolve) => require(['@/pages/pawnmanage/pawninfo.vue'], resolve),
					meta: {
						privilegeId: ['3001', '3002', '3003'],
						parentName: 'pawnmanage',
						pageTitle: '押品信息',
					}
				},
				{
					path: '/pawnout',
					name: 'pawnout',
					component: (resolve) => require(['@/pages/pawnmanage/out/pawnout.vue'], resolve),
					meta: {
						privilegeId: ['3002'],
						parentName: 'pawnmanage',
						activeName: 'pawnout',
						pageTitle: '出库',
					}
				},
				{
					path: '/applyout',
					name: 'applyout',
					component: (resolve) => require(['@/pages/pawnmanage/out/applyout.vue'], resolve),
					meta: {
						privilegeId: ['3002'],
						parentName: 'pawnmanage',
						activeName: 'pawnout',
						pageTitle: '申请出库',
					}
				},
				{
					path: '/forceout',
					name: 'forceout',
					component: (resolve) => require(['@/pages/pawnmanage/out/forceout.vue'], resolve),
					meta: {
						privilegeId: ['3002'],
						parentName: 'pawnmanage',
						activeName: 'pawnout',
						pageTitle: '出库申请',
					}
				},
				{
					path: '/pawnreview',
					name: 'pawnreview',
					component: (resolve) => require(['@/pages/pawnmanage/review/pawnreview.vue'], resolve),
					meta: {
						privilegeId: ['3003'],
						parentName: 'pawnmanage',
						activeName: 'pawnreview',
						pageTitle: '出入审批',
					}
				},
				{
					path: '/revieworderin',
					name: 'revieworderin',
					component: (resolve) => require(['@/pages/pawnmanage/review/revieworderin.vue'], resolve),
					meta: {
						privilegeId: ['3003'],
						parentName: 'pawnmanage',
						activeName: 'pawnreview',
						pageTitle: '入库审批',
					}
				},
				{
					path: '/revieworderout',
					name: 'revieworderout',
					component: (resolve) => require(['@/pages/pawnmanage/review/revieworderout.vue'], resolve),
					meta: {
						privilegeId: ['3003'],
						parentName: 'pawnmanage',
						activeName: 'pawnreview',
						pageTitle: '出库审批',
					}
				},
				{
					path: '/inreport',
					name: 'inreport',
					component: (resolve) => require(['@/pages/report/inreport.vue'], resolve),
					meta: {
						privilegeId: ['4001'],
						parentName: 'report',
						activeName: 'inreport',
						pageTitle: '进件统计',
					}
				},
				{
					path: '/loanreport',
					name: 'loanreport',
					component: (resolve) => require(['@/pages/report/loanreport.vue'], resolve),
					meta: {
						privilegeId: ['4002'],
						parentName: 'report',
						activeName: 'loanreport',
						pageTitle: '放款统计',
					}
				},
				{
					path: '/accountReport',
					name: 'accountReport',
					component: (resolve) => require(['@/pages/report/accountReport.vue'], resolve),
					meta: {
						privilegeId: ['4003'],
						parentName: 'report',
						activeName: 'accountReport',
						pageTitle: '台账报表',
					}
				}
			]
		}
	]
})



