<template>
	<i :class="classes" :style="styles"></i>
</template>
<script>
	let eicon = 'iconfont';
	export default {
		name: 'eicon',
		props: {
			type: String,
			size: [Number, String],
			color: String
		},
		computed: {
			classes () {
				return `${eicon} ${this.type}`;
			},
			styles () {
				let style = {};

				if (this.size) {
					style['font-size'] = `${this.size}px`;
				}

				if (this.color) {
					style.color = this.color;
				}

				return style;
			}
		}
	};
</script>



