export default{
	install(Vue, options)
	{
		Vue.prototype.toDecimal = function (x) {//保留两位小数
			var f = parseFloat(x);
			if (isNaN(f)) {
				return false;
			}
			var f = Math.round(x * 100) / 100;
			var s = f.toString();
			var rs = s.indexOf('.');
			if (rs < 0) {
				rs = s.length;
				s += '.';
			}
			while (s.length <= rs + 2) {
				s += '0';
			}
			return s;
		};
		Vue.prototype.fmoney = function (s, n) {//货币格式化
			if (s == undefined || s == null || s == '') {
				return '-'
			};
			n = n > 0 && n <= 20 ? n : 2;
			s = parseFloat((s + "").replace(/[^\d\.-]/g, "")).toFixed(n) + "";
			var l = s.split(".")[0].split("").reverse(),
				r = s.split(".")[1];
			var t = "";
			for (var i = 0; i < l.length; i++) {
				t += l[i] + ((i + 1) % 3 == 0 && (i + 1) != l.length ? "," : "");
			}
			return t.split("").reverse().join("") + "." + r;
		};
		Vue.prototype.intersection = function (a, b) { // 取数组交集
			var result = [];
			if (a.length == 0 || b.length == 0) {
				return result;
			} else {
				for (var i = 0; i < b.length; i++) {
					var temp = b[i];
					for (var j = 0; j < a.length; j++) {
						if (temp == a[j]) {
							result.push(temp);
							break;
						}
					}
				}
				return result;
			}
		};
		Vue.prototype.initDefaultDate =function (n,timeUnit) {//根据时间获取N天，月，年后的时间
			var curr_date = new Date();
			if (timeUnit === 'd') {
				curr_date.setDate(curr_date.getDate() + n);
			} else if (timeUnit === 'M') {
				curr_date.setMonth(curr_date.getMonth() + n);
			} else if (timeUnit === 'y') {
				curr_date.setFullYear(curr_date.getFullYear() + n);
			}
			var strYear = curr_date.getFullYear();
			var strMonth = curr_date.getMonth()+1;
			var strDay = curr_date.getDate();
			var strHours = curr_date.getHours();
			var strMinutes = curr_date.getMinutes();

			var datastr = strYear + '-' + formatNumber(strMonth) + '-'
				+ formatNumber(strDay);
			return datastr;
		};
		Vue.prototype.insertStr =function (str,flg,sn){//插入字符串
			var newstr="";
			for(var i=0;i<str.length;i+=sn){
				var tmp=str.substring(i, i+sn);
				newstr+=tmp+flg;
			}
			return newstr;
		};
		Vue.prototype.encode =function (str,flg,sn){//插入字符串
			var newstr="";
			for(var i=0;i<str.length;i+=sn){
				var tmp=str.substring(i, i+sn);
				newstr+=tmp+flg;
			}
			return newstr;
		};
	}
};
function formatNumber(value){
	return (value < 10 ? '0' : '') + value;
}




