// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from "vue";
import App from "./App";
import router from "./router";
import iView from "iview";
import Axios from "@/http";
import Store from "./store";
import EIcon from "@/components/eicon";
import VueAwesomeSwiper from "vue-awesome-swiper";
import _ from "lodash";
import util from "./util";
import * as filters from '@/filters'
import "iview/dist/styles/iview.css";
import "swiper/dist/css/swiper.css";
import "@/assets/iconfont.css";
import "@/assets/animate.css";
Vue.use(VueAwesomeSwiper);
Vue.use(_);
Vue.use(iView);
Vue.use(util);
let baseInfo = {
	baseUrl: 'http://192.168.0.221:8081/ecrs',
	imgUrl: 'http://192.168.0.221:8081/staticresource',
	socketUrl: 'ws://192.168.0.221:8081/ecrs/wmessage.ws/'
};
sessionStorage.setItem('baseInfo', JSON.stringify(baseInfo));//储存基础配置信息
Axios.defaults.baseURL = baseInfo.baseUrl;
Vue.prototype.$http = Axios;
Vue.config.productionTip = false;
router.beforeEach((to, from, next) => {
	let userInfo = JSON.parse(sessionStorage.getItem('userInfo'));
	let whiteList = ['/login'];// 白名单
	iView.LoadingBar.start();
	document.title =to.meta.pageTitle;
	Store.dispatch('changeActiveName',to.meta.activeName);
	//判断是否拉取到了用户信息
	if (_.isEmpty(userInfo)) {
		//为空判断是否在白名单内，否则转去登陆
		if (whiteList.indexOf(to.path) !== -1) {
			next()
		} else {
			next('login');
		}
	} else {
		//检验是否有进入权限
		if (to.path == '/login') {
			sessionStorage.removeItem('userInfo');
			next()
		} else {
			if (_.intersection(userInfo.privilegeList, to.meta.privilegeId).length > 0) {
				next();
			} else {
				iView.Message.error('权限错误!');
				next(false);
				iView.LoadingBar.finish();
			}
		}
	}
});
router.afterEach((to, from, next) => {
	iView.LoadingBar.finish();
	window.scrollTo(0, 0);
});
Vue.component('e-icon', EIcon);
Object.keys(filters).forEach(key => {//注册过滤器
	Vue.filter(key, filters[key])
})
var EventUtil = {
	addHandler: function (element, type, handler) {
		if (element.addEventListener) {
			element.addEventListener(type, handler, false);
		} else if (element.attachEvent) {
			element.attachEvent("on" + type, handler);
		} else {
			element["on" + type] = handler;
		}
	}
};
EventUtil.addHandler(window, "offline", function () {
	iView.Message.error('未连接网络')
});
Date.prototype.Format = function (fmt) {//增加时间的格式化方法
	var o = {
		"M+": this.getMonth() + 1, //月份
		"d+": this.getDate(), //日
		"h+": this.getHours(), //小时
		"m+": this.getMinutes(), //分
		"s+": this.getSeconds(), //秒
		"q+": Math.floor((this.getMonth() + 3) / 3), //季度
		"S": this.getMilliseconds() //毫秒
	};
	if (/(y+)/.test(fmt)) fmt = fmt.replace(RegExp.$1, (this.getFullYear() + "").substr(4 - RegExp.$1.length));
	for (var k in o)
		if (new RegExp("(" + k + ")").test(fmt)) fmt = fmt.replace(RegExp.$1, (RegExp.$1.length == 1) ? (o[k]) : (("00" + o[k]).substr(("" + o[k]).length)));
	return fmt;
};
new Vue({
	el: '#app',
	router,
	store: Store,
	template: '<App/>',
	components: {App}
});



