<template>
	<div class="content">
		<div class="main">
			<div class="login-main">
				<div class="logo-wrap">
					<div class="left">
						<img src="../assets/logo.png" title="图片错误"/>
					</div>
					<div class="right">
						易捷-新网风控管理系统
					</div>
				</div>
				<div class="login-form">
					<div class="form-content">
						<form @keyup.enter="submit">
							<div class="item">
								<div class="title">用户名</div>
								<div class="input-wrap"
									 :class="{ 'input-wrap-ac': nameAc, 'input-error': name_error.length>0 }">
									<Icon class="self-icon" type="android-person" size="20" color="#7cb6d4"></Icon>
									<input class="self-input" v-model="user_name" :maxlength="16" type="text"
										   placeholder="请输入用户名"
										   @focus="nameAc=true" @blur="checkName"/>
								</div>
								<div class="tip-info">{{name_error}}</div>
							</div>
							<div class="item">
								<div class="title">密码</div>
								<div class="input-wrap"
									 :class="{ 'input-wrap-ac': pswAc, 'input-error': psw_error.length>0 }">
									<Icon class="self-icon" type="android-person" size="20" color="#7cb6d4"></Icon>
									<input class="self-input" v-model="user_psw" :maxlength="20" type="password"
										   placeholder="请输入密码"
										   @focus="pswAc=true" @blur="checkPsw"/>
								</div>
								<div class="tip-info">{{psw_error}}</div>
							</div>
							<div class="item">
								<div class="title">验证码</div>
								<div class="code-box">
									<div class="input-wrap code-wrap"
										 :class="{ 'input-wrap-ac': codeAc, 'input-error': code_error.length>0 }">
										<input class="self-input code-input" v-model="user_code" :maxlength="4"
											   type="text"
											   placeholder="请输入验证码" @focus="codeAc=true" @blur="checkCode"/>
									</div>
									<div class="spin-wrap"  @click="refreshCode" >
										<img v-if="showCode" class="code-img" alt="验证码" title="点击重试"
											 :src="baseInfo.baseUrl+code_img"/>
										<Spin fix v-else>
											<Icon type="load-c" size=18  class="spin-icon-load"></Icon>
										</Spin>

									</div>
								</div>
							</div>
							<i-button type="info" long :disabled="submiting" :loading="submiting" class="submit-btn"
									@click="submit">
								登录
							</i-button>
						</form>
					</div>
				</div>
			</div>
			<!--<p class="copyRight">
				Copyright © 2017 易捷金融服务外包有限公司
			</p>-->
		</div>
	</div>
</template>
<script>
	export default {
		name: 'login',
		data () {
			return {
				nameAc: false,
				pswAc: false,
				codeAc: false,
				name_error: '',
				psw_error: '',
				code_error: '',
				user_name: '',
				user_psw: '',
				user_code: '',
				code_img: '',
				showCode: false,
				submiting: false,
				baseInfo: {}
			}
		},
		created: function () {
			this.baseInfo = JSON.parse(sessionStorage.getItem('baseInfo'));
			if (_.isEmpty(JSON.parse(sessionStorage.getItem('userInfo')))) {
				this.loadCode();
			} else {
				this.$router.push('message')
			}
		},
		methods: {
			submit: function () {//登陆
				let _this = this;
				if (_this.checkName() && _this.checkPsw() && _this.checkCode()) {
					_this.submiting = true;
					this.$http.post('/user/login', {
						userName: _this.user_name,
						password: _this.user_psw,
						verificationCode: _this.user_code
					})
						.then(function (response) {
							var res = response.data;
							_this.submiting = false;
							if (res.code == 1) {//登陆成功
								sessionStorage.setItem('userInfo', JSON.stringify(res.data));//储存用户信息
								_this.$router.replace({path: '/message'});
							} else if (res.code == 0) {
								_this.$Message.error(res.msg);
								_this.loadCode();
							} else {
								_this.$Message.error('登陆失败,请联系系统管理员');
							}
						})
						.catch(function (error) {
							_this.submiting = false;
							_this.$Message.error({
								content: error
							});
						});
				}
			},
			refreshCode: function () {//刷新验证码
				this.loadCode()
			},
			checkName: function () {
				let _this = this;
				let name = /^[a-z0-9_-]{3,16}$/;
				_this.nameAc = false;
				if (_this.user_name.length == 0) {
					_this.name_error = '用户名不能为空';
					return false;
				} else if (!name.exec(_this.user_name)) {
					_this.name_error = '用户名应由3至16位字母或数字组成';
					return false;
				} else {
					_this.name_error = '';
					return true;
				}
			},
			checkPsw: function () {
				let _this = this;
				let psw = /^[a-z0-9_-]{6,20}$/;
				_this.pswAc = false;
				if (_this.user_psw.length == 0) {
					_this.psw_error = '密码不能为空';
					return false;
				} else if (!psw.exec(_this.user_psw)) {
					_this.psw_error = '密码应由6至20位字母或数字组成'
					return false;
				} else {
					_this.psw_error = '';
					return true;
				}
			},
			checkCode: function () {
				let _this = this;
				let code = /^[a-zA-Z0-9]/;
				_this.codeAc = false;
				if (!code.exec(_this.user_code) || _this.user_code.length != 4) {
					_this.code_error = '验证码错误';
					return false;
				} else {
					_this.code_error = '';
					return true;
				}
			},
			loadCode: function () {
				let _this = this;
				_this.showCode = false;
				this.$http.get('/admin/index')
					.then(function (response) {
						_this.showCode = true;
						_this.code_img = response.data.verificationCode;
					})
					.catch(function (error) {
						_this.$Message.error({
							content: '验证码获取失败，请稍后再试！',
							duration: 3
						});
						_this.showCode = false;
					});
			}
		}
	}
</script>
<style scoped>
	.content {
		width: 100%;
		height: 100%;
		background: url("../assets/login.jpg") no-repeat top center / 1920px 600px;
		min-width: 1200px;
	}

	.main {
		width: 1200px;
		height: 100%;
		margin: 0 auto;
		position: relative;
	}
	.copyRight{
		text-align: center;
		position: absolute;
		width: 300px;
		left: 50%;
		margin-left: -150px;
		bottom: 50px;
		color: #666;
		font-size: 14px;
	}
	.login-main {
		margin-left: 560px;
		padding-top: 200px;
	}

	.logo-wrap {
		display: flex;
		flex-direction: row;
		justify-content: center;
		align-items: flex-end;
		width: 400px;
	}

	.left {
		font-size: 0;
	}

	.right {
		color: #fff;
		font-size: 16px;
		height: 35px;
		line-height: 35px;
		padding-left: 25px;
		border-left: 1px solid rgba(255, 255, 255, .4);
		margin-left: 25px;
	}

	.login-form {
		width: 400px;
		height: 380px;
		background: #fff;
		border-radius: 20px;
		box-shadow: 0 8px 8px #a5a5a5;
		padding-top: 40px;
		margin-top: 38px;
	}

	.form-content {
		width: 350px;
		margin: 0 auto;
	}

	.title {
		color: #808080;
		font-size: 12px;
		padding-bottom: 12px;
		padding-left: 5px;
	}

	.input-wrap {
		height: 35px;
		width: 340px;
		border-radius: 5px;
		border: 1px solid #8fd5ff;
		display: flex;
		flex-direction: row;
		align-items: center;
		margin-left: 5px;
	}

	.input-wrap-ac {
		box-shadow: 0 0 5px #b0e0f9;
	}

	.input-error {
		border: 1px solid #ed3f14;
		box-shadow: 0 0 5px #E14013;
	}

	.self-icon {
		padding: 0 10px;
	}

	.self-input {
		width: 290px;
		border: none;
		font-size: 16px;
		color: #3d4858;
		background-color: transparent;
		outline: none;
	}

	.tip-info {
		height: 20px;
		color: #ed3f14;
		padding-top: 5px;
		text-align: right;
		padding-right: 5px;
	}

	.code-wrap {
		width: 200px;
		padding: 0 10px;
	}

	.code-input {
		width: 180px;
	}

	.code-box {
		display: flex;
		flex-direction: row;
		justify-content: space-between;
		align-items: center;
	}
	.spin-wrap {
		position: relative;
		margin-right: 5px;
		border: 1px solid #8fd5ff;
		width: 120px;
		height: 35px;
		border-radius: 5px;
		display: block;
		overflow: hidden;
		cursor: pointer;
	}

	.spin-icon-load {
		animation: ani-demo-spin 1s linear infinite;
	}

	@keyframes ani-demo-spin {
		from {
			transform: rotate(0deg);
		}
		50% {
			transform: rotate(180deg);
		}
		to {
			transform: rotate(360deg);
		}
	}

	.code-img {
		width: 100%;
		height: 100%;
	}

	.submit-btn {
		font-size: 16px;
		letter-spacing: 5px;
		margin-top: 35px;
	}
</style>



