let empty = value => {//为空返回‘-’
	if (value || value==0) {
		return value
	} else {
		return '-'
	}
};
let fmoney = (s,n) => {//格式化money
	if (s == undefined || s == null || s == '') {
		return s
	}
	n = n > 0 && n <= 20 ? n : 2;
	s = parseFloat((s + "").replace(/[^\d\.-]/g, "")).toFixed(n) + "";
	var l = s.split(".")[0].split("").reverse(),
		r = s.split(".")[1];
	var t = "";
	for (var i = 0; i < l.length; i++) {
		t += l[i] + ((i + 1) % 3 == 0 && (i + 1) != l.length ? "," : "");
	}
	return t.split("").reverse().join("") + "." + r;
};
let fstatus = value => {//1：通过，2：不通过，3：补充资料，4：正在预审
	let result='';
	switch (value) {
		case '1':
			result='通过';
			break;
		case '2':
			result='不通过';
			break;
		case '3':
			result='补充资料';
			break;
		case '4':
			result='正在预审';
			break;
		default:
			result='';
	};
	return result;
};
let fDate = (value,fmt) => {//格式化date
	if (typeof value == 'number') {
		var time = new Date(value);
		fmt=fmt?fmt:"yyyy-MM-dd";
		var o = {
			"M+": time.getMonth() + 1, //月份
			"d+": time.getDate(), //日
			"h+": time.getHours(), //小时
			"m+": time.getMinutes(), //分
			"s+": time.getSeconds(), //秒
			"q+": Math.floor((time.getMonth() + 3) / 3), //季度
			"S": time.getMilliseconds() //毫秒
		};
		if (/(y+)/.test(fmt)) fmt = fmt.replace(RegExp.$1, (time.getFullYear() + "").substr(4 - RegExp.$1.length));
		for (var k in o)
			if (new RegExp("(" + k + ")").test(fmt)) fmt = fmt.replace(RegExp.$1, (RegExp.$1.length == 1) ? (o[k]) : (("00" + o[k]).substr(("" + o[k]).length)));
		return fmt;
	} else {
		return value
	}
};
let fchannelCode = value => {//转换放款渠道
	let result='';
	switch (value) {
		case 1802:
			result='外贸信托';
			break;
		case 1804:
			result='新网银行';
			break;
		default:
			result= '未知渠道';
	};
	return result;
};
export { empty,fmoney,fstatus,fDate,fchannelCode }


