<template>
	<div class="layout">
		<div class="layout-header">
			<div class="logo-wrap">
				<router-link class="logo" to="/">
					<img src="../assets/logo.png" title="图片错误"/>
				</router-link>
				<div class="toggleMenu" @click="toggleMenu">
					<Icon type="navicon" :size="20" color="#20abff"></Icon>
				</div>
			</div>
			<div class="user-box">
				<Dropdown trigger="click" placement="bottom-end" @on-click="userCenter">
					<a class="user-name" href="javascript:void(0)">
						<e-icon type="icon-yonghu" size="30" class="user-icon"></e-icon>
						{{userInfo.userName}}
						<Icon type="arrow-down-b" style="margin-left: 5px"></Icon>
					</a>
					<Dropdown-menu slot="list">
						<Dropdown-item name="psw">修改密码</Dropdown-item>
					</Dropdown-menu>
				</Dropdown>
				<div class="message-wrap" @click="$router.push('message')">
					<Badge :count="badgeCount" class="message">
						<Icon type="android-notifications" size="30"></Icon>
					</Badge>
				</div>
				<div class="quit-btn" @click="loginOut">
					<e-icon type="icon-iconzhuangtaituichu" class="quit" size="30"></e-icon>
				</div>
			</div>
		</div>
		<div class="main-content">
			<transition name="slide">
				<div class="menu-wrap" v-show="showMenu">
					<keep-alive>
						<Menu class="menu" ref="menu" :active-name="activeName"
							  :open-names="openNames"
							  theme="dark" width="auto" @on-select="routeTo"
							  :accordion="true" style="width:240px">
							<Submenu name="approve"
									 v-if="intersection(privilegeList,['1000','1001','1002','1003','1004','1005','1006','1080']).length>0">
								<template slot="title">
									<e-icon type="icon-shuru"></e-icon>
									<span class="sub-title-text">贷款审批</span>
								</template>
								<Menu-item name="director"
										   v-if="intersection(privilegeList,['1080']).length>0">
									<span class="sub-title-text">金融专员</span>
								</Menu-item>
								<Menu-item name="creditstart"
										   v-if="intersection(privilegeList,['1001']).length>0">
									授信初审
								</Menu-item>
								<Menu-item name="creditfinish"
										   v-if="intersection(privilegeList,['1002']).length>0">
									授信复审
								</Menu-item>
								<Menu-item name="loanstart"
										   v-if="intersection(privilegeList,['1003']).length>0">
									放款初审
								</Menu-item>
								<Menu-item name="loanongoing"
										   v-if="intersection(privilegeList,['1004']).length>0">
									放款复审
								</Menu-item>
								<Menu-item name="loanfinish"
										   v-if="intersection(privilegeList,['1005']).length>0">
									放款终审
								</Menu-item>
								<Menu-item name="loanconfirm"
										   v-if="intersection(privilegeList,['1006']).length>0">
									账务确认
								</Menu-item>
							</Submenu>
							<Submenu name="afterloan"
									 v-if="intersection(privilegeList,['2000','2001','2002']).length>0">
								<template slot="title">
									<e-icon type="icon-huankuandan"></e-icon>
									<span class="sub-title-text">贷后管理</span>
								</template>
								<Menu-item name="inloan"
										   v-if="intersection(privilegeList,['2001']).length>0">
									在贷客户
								</Menu-item>
								<Menu-item name="outloan"
										   v-if="intersection(privilegeList,['2002']).length>0">
									清贷客户
								</Menu-item>
							</Submenu>
							<Submenu name="pawnmanage"
									 v-if="intersection(privilegeList,['3000','3001','3002','3003']).length>0">
								<template slot="title">
									<e-icon type="icon-yewujindu"></e-icon>
									<span class="sub-title-text">押品管理</span>
								</template>
								<Menu-item name="pawnin"
										   v-if="intersection(privilegeList,['3001']).length>0">
									入库
								</Menu-item>
								<Menu-item name="pawnout"
										   v-if="intersection(privilegeList,['3002']).length>0">
									出库
								</Menu-item>
								<Menu-item name="pawnreview"
										   v-if="intersection(privilegeList,['3003']).length>0">
									出入审批
								</Menu-item>
							</Submenu>
							<Submenu name="report"
									 v-if="intersection(privilegeList,['4000','4001','4002','4003','4004']).length>0">
								<template slot="title">
									<e-icon type="icon-tubiao"></e-icon>
									<span class="sub-title-text">统计报表</span>
								</template>
								<Menu-item name="inreport"
										   v-if="intersection(privilegeList,['4001']).length>0">
									进件统计
								</Menu-item>
								<Menu-item name="loanreport"
										   v-if="intersection(privilegeList,['xxxx']).length>0">
									放款统计
								</Menu-item>
								<Menu-item name="accountReport"
										   v-if="intersection(privilegeList,['4003']).length>0">
									台账报表
								</Menu-item>
							</Submenu>
							<Menu-item name="message"
									   v-if="intersection(privilegeList,['5000']).length>0">
								<e-icon type="icon-xiaoxi"></e-icon>
								<span class="sub-title-text">我的消息</span>
							</Menu-item>
							<Menu-item name="system"
									   v-if="intersection(privilegeList,['6000']).length>0">
								<e-icon type="icon-xitongguanli"></e-icon>
								<span class="sub-title-text">系统管理</span>
							</Menu-item>
						</Menu>
					</keep-alive>
				</div>
			</transition>
			<div class="layout-content" id="scrollWrap">
				<div class="layout-content-main">
					<transition name="fade" mode="out-in">
						<router-view></router-view>
					</transition>
				</div>
			</div>
		</div>
	</div>
</template>
<script>
	export default {
		data () {
			return {
				showMenu: true,
				privilegeList: [],
				openNames: ['message'],
				userInfo: {},
				baseInfo: {}
			}
		},
		created: function () {
			var _this = this;
			var userInfo = JSON.parse(sessionStorage.getItem('userInfo'));//获取用户信息
			_this.baseInfo = JSON.parse(sessionStorage.getItem('baseInfo'));
			if (userInfo) {
				_this.userInfo = userInfo;
				_this.privilegeList = userInfo.privilegeList;
				_this.openNames[0] = _this.$route.meta.parentName;
				_this.webSocket(userInfo.roleId, userInfo.userId);
			} else {
				_this.$Message.error({
					content: '未获取到用户资料，请重新登录！',
					duration: 3
				});
				_this.loginOut();
			}
		},
		computed: {
			badgeCount(){
				let list = this.$store.getters.useMsgList;
				return _.filter(list, {status: 0}).length;
			},
			activeName(){
				return this.$store.getters.activeName
			}
		},
		methods: {
			routeTo: function (e) {
				let _this = this;
				_this.$router.push(e);
			},
			toggleMenu: function () {
				this.showMenu = !this.showMenu;
			},
			loginOut: function () {
				sessionStorage.removeItem('userInfo');
				this.$router.replace('/login');
			},
			userCenter: function (name) {
				if (name == 'psw') {
					this.$router.push('changepsw')
				}
			},
			showNotice: function (data) {
				var _this = this;
				var Notification = window.Notification || window.mozNotification || window.webkitNotification;
				if (Notification) {
					Notification.requestPermission(function (status) {
						if ("granted" != status) {
							_this.$Notice.info({
								title: title,
								desc: msg
							});
							return;
						} else {
							var tag = "ejie" +Date.parse(new Date());
							var title = '';
							var content = '';
							if (data.length > 1) {
								title = '您有多条消息未读！';
								content = '请点击查看！'
							} else {
								title = data.title;
								content = data.content
							}
							var notify = new Notification(
								title,
								{
									dir: 'auto',
									lang: 'zh-CN',
									tag: tag,
									icon: 'https://www.ejie365.cn/favicon.ico',
									silent: false,
									sound: 'https://wechat.ejie365.cn/rang.mp3',
									body: content
								}
							);
							notify.onclick = function () {
								//如果通知消息被点击,通知窗口将被激活
								window.focus();
								_this.$router.push('message');
								/*var s=[];
								 _.filter(data,function (item) {
								 s.push({loanId:item.loanId,status:1});
								 item.status=1;
								 });
								 window.webSocket.send(JSON.stringify(s));*/
								_this.$store.dispatch('changeStatus');
								notify.close();
							};
							notify.onerror = function () {
								_this.$Notice.info({
									title: title,
									desc: content
								});
							};
							notify.onshow = function () {
								setTimeout(function () {
									notify.close();
								}, 5000)
							};
						}
					});
				} else {
					console.log("您的浏览器不支持桌面消息");
				}
			},
			webSocket: function (roleId, userId) {
				var _this = this;
				var webSocket = new WebSocket(`${_this.baseInfo.socketUrl}${roleId}/${userId}`);
				if (webSocket) {
					window.webSocket = webSocket;
					webSocket.onopen = function (event) {
						// 发送一个初始化消息
						webSocket.send('老司机发车了！');
					};
					// 监听消息
					webSocket.onmessage = function (event) {
						let data = JSON.parse(event.data);
						_this.$store.dispatch('addMessage', data);
						_this.showNotice(data);

					};
					webSocket.onerror = function (error) {
						_this.$Notice.error({
							title: '通信错误',
							desc: '通信服务出错，请刷新重试，或联系管理员！',
							duration: 4
						});
					}
					// 监听关闭
					webSocket.onclose = function (event) {
						_this.$Notice.error({
							title: '服务错误',
							desc: '通信服务关闭!请重新连接，或联系管理员！',
							duration: 4
						});
					};
				} else {
					_this.$Notice.warning({
						title: '连接失败',
						desc: '浏览器不支持websocket，请使用谷歌浏览器！'
					});
				}

			}
		}
	}
</script>
<style scoped>
	.layout {
		border: 1px solid #d7dde4;
		background: #f5f7f9;
		position: relative;
		overflow: hidden;
		width: 100%;
		height: 100%;
		padding-top: 80px;
	}
	.layout-content {
		min-height: 200px;
		overflow-y: auto;
		background: #fafafa;
		padding: 0 30px;
		flex: 1;
	}
	.layout-content-main {
		background-color: #fafafa;
	}
	.menu-wrap {
		width: 240px;
		background-color: #2a3341;
		overflow: hidden;
	}
	.menu {
		background-color: #2a3341;
	}
	.ivu-menu-dark.ivu-menu-vertical .ivu-menu-opened {
		background-color: #2a3341;
	}

	.layout-menu-left {
		background: #2a3341;
		overflow: hidden;
	}

	.layout-header {
		height: 80px;
		background-color: #fff;
		box-shadow: 0 3px 5px rgba(0, 0, 0, .1);
		display: flex;
		align-items: center;
		justify-content: space-between;
		padding-right: 30px;
		margin-top: -80px;
	}
	.toggleMenu {
		position: absolute;
		right: -20px;
		top: 20px;
		width: 40px;
		height: 40px;
		border-radius: 20px;
		background-color: #e5f4ff;
		display: flex;
		align-items: center;
		justify-content: center;
	}
	.user-box {
		display: flex;
		justify-content: space-between;
		align-items: center;
	}
	.user-name {
		font-size: 20px;
		color: #52647f;
		margin-left: 40px;
		display: flex;
		justify-content: center;
		align-items: center;
		cursor: pointer;
		transition: all .5s;
	}
	.user-icon {
		margin-right: 10px;
	}
	.user-name:hover {
		color: #20abff;
	}
	.message-wrap {
		margin-left: 20px;
		color: #52647f;
		transition: all .5s;
	}
	.message-wrap:hover {
		cursor: pointer;
		color: #20abff;
	}
	.quit-btn {
		margin-left: 40px;
		color: #52647f;
		transition: all .5s;
	}
	.quit-btn:hover {
		cursor: pointer;
		color: #20abff;
	}
	.logo-wrap {
		justify-content: center;
		align-items: center;
		display: flex;
		height: 80px;
		background-color: #20abff;
		width: 240px;
		position: relative;
	}
	.logo {
		width: 100%;
		max-width: 142px;
		max-height: 50px;
		margin: 15px auto;
		overflow: hidden;
	}
	.main-content {
		display: flex;
		justify-content: space-between;
		height: 100%;
		width: 100%;
	}
	.layout-hide-text .sub-title-text {
		display: none;
	}

	.layout-hide-text .ivu-menu-item {
		padding-left: 24px;
	}
	.logo > img {
		width: 100%;
	}
	.slide-enter-active {
		transition: all .3s ease;
	}
	.slide-leave-active {
		transition: all .5s;
	}
	.slide-enter, .slide-leave-to
	{
		width: 0px;
	}
	a {
		color: #000;
	}
	a:hover {
		color: cornflowerblue;
	}
</style>



