import "babel-polyfill"
import Vue from "vue";
import VueX from "vuex";
import _ from "lodash";
Vue.use(VueX);
export default new VueX.Store({
	state: {
		msgList: [],
		creditTimeLimit:0,
		menuActiveName:'message'
	},
	getters: {
		useMsgList: state => {
			return state.msgList;
		},
		activeName:state=>{
			return state.menuActiveName
		}
	},
	mutations: {
		addMessage(state, msg){
			state.msgList = _.concat(state.msgList, msg);
		},
		changeStatus(state){
			_.filter(state.msgList, function (item) {
				if (item.status == 0) {
					item.status = 1;
					window.webSocket.send(JSON.stringify({loanId: item.loanId, status: item.status}))
				}
				return;
			})
		},
		changeActiveName(state,name){
			state.menuActiveName=name
		}
	},
	actions: {
		addMessage(context, msg){
			context.commit('addMessage', msg)
		},
		changeStatus(context){
			context.commit('changeStatus')
		},
		changeActiveName(context, name){
			context.commit('changeActiveName', name)
		}
	}
})



